import { Component, ElementRef, EventEmitter, Input, OnInit, Output, SimpleChanges, ViewChild } from '@angular/core';
import { VhAlgorithm, VhQuerySales } from 'ionic-vhframeworks';
import { Subscription } from 'rxjs';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-search-voucher',
  templateUrl: './search-voucher.component.html',
  styleUrls: ['./search-voucher.component.scss']
})
export class SearchVoucherComponent implements OnInit {
  @ViewChild("inputSearchSales") myInputField: ElementRef;
  @Input("searchProductList") searchProductList: boolean = true;
  @Input() barcode: any;
  @Input() id_product_delete: string;

  @Output("closeSearchProductList") closeSearchProductList = new EventEmitter<any>();
  @Output() clearBarcode = new EventEmitter()
  @Input() default_price_type: any;
  public observableAllProducts: Subscription
  public indexTab: any = 0;
  public id_branch: string = this.vhQuerySales.getDefaultBranch()._id
  public tabs: any = []
  public listVoucher: any[] = [];
  public goodList: any[] = [];
  public searchList: any[] = [];
  public listAllProducts: any[] = [];
  public typeGoods = 0;
  public search: string = '';
  public tempVal: string = '';
  public unable_vouchers_all_branch: any = this.vhQuerySales.getLocalAppSettingNameStation('sales_program').unable_vouchers_all_branch                     // cho phép áp dụng voucher cho toàn bộ chi nhánh


  constructor(
    public vhAlgorithm: VhAlgorithm,
    private vhQuerySales: VhQuerySales,
    private vhComponent: VhComponent,
    private languageService: LanguageService,

  ) { }

  ngOnInit(): void {
    this.getVoucherCodes();
  }

  getVoucherCodes() {
    // lấy danh sách voucher đã phát hành
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      // lấy toàn bộ danh sách thẻ bán trạng thái mới phát hành
      Promise.all([
        this.vhQuerySales.getVoucherCodes_byFields({ status: { $eq: 1 } }, {}, {}, 0),
        // this.vhQuerySales.getVouchers()
      ]).then(([voucherCode, ]: any) => {
        voucherCode.forEach(async (code) => {
          let voucher_release = await this.vhQuerySales.getVoucher_Code_Name_Release(code._id) 
          // 
          // let voucher = vouchers.find(item => item._id == code.id_voucher);
          if (voucher_release) {
            code['name'] = voucher_release['name_voucher'];
            code['name_voucher_release'] = voucher_release['name_voucher_release'] ;
            code['price'] = voucher_release['units'][0].price;
            code['products'] = voucher_release['products'].map(e => {
              return {
                ...e, name: this.vhQuerySales.getlocalDetailProduct(e.id_subproduct ? e.id_subproduct : e.id_product).name,
                unit: this.vhQuerySales.getUnit_byRatio(this.vhQuerySales.getlocalDetailProduct(e.id_subproduct ? e.id_subproduct : e.id_product).units, 1).unit
              }
            })
          }
        });
        this.listVoucher = voucherCode;
        this.searchList = this.listVoucher;
        
        
        this.vhComponent.hideLoading(0);
      }, error => {
        
        this.vhComponent.hideLoading(0);
      })
    })
  }


  ngOnChanges({ barcode, id_product_delete }: SimpleChanges): void {
    if (id_product_delete) {
      if (id_product_delete.currentValue) {
        const index = this.listVoucher.findIndex(item => item._id == id_product_delete.currentValue);
        this.listVoucher[index].choose = false;
      }
    }
    if (barcode)
      if (barcode.currentValue) {
        if (barcode.currentValue.toString().length > 5) {
          let product = this.listAllProducts.filter(item => item.barcode == barcode.currentValue);
          if (product[0]) { this.chooseProduct(product[0]); this.clearBarcode.emit(true) }
          else {
            this.vhComponent.alertMessageDesktop('error', this.languageService.translate("Not found"))
            this.clearBarcode.emit(true)
          }
        }
      }
  }

  ngOnDestroy() {
    if (this.observableAllProducts) this.observableAllProducts.unsubscribe();
  }
  ngAfterViewInit() {
    this.myInputField.nativeElement.focus();
  }
  acticeFocus() {
    this.myInputField.nativeElement.focus();
  }

  goBack() {
    this.closeSearchProductList.emit(false);
  }
  // tìm voucher code theo _id
  searchProduct(value: string) {
    this.tempVal = this.vhAlgorithm.changeAlias(value.toLowerCase());
    if (value.length) {
      this.listVoucher = this.vhAlgorithm.searchList(this.tempVal, this.searchList, ['code']);
    } else {
      this.listVoucher = this.searchList;
    }
  }

  // config dữ liệu, để lưu vào payment_detais
  chooseProduct(item) {
    item.choose = true
    let dataConfig = {
      id_voucher_code: item._id,
      name: item.name,
      name_voucher_release: item.name_voucher_release,
      id_voucher: item.id_voucher,
      code: item.code,
      vtype: 1,
      price: item.price,
      products: item.products
    }
    this.closeSearchProductList.emit(dataConfig);
  }


}
