  getPrint() {
    electron.ipcRenderer.send("allPrint");
    electron.ipcRenderer.on("printName", (event, data) => {
      this.list_printer = data
    });
  }
  updatePrint() {
    let value = { ...this.printer }
    delete value._id;
    this.vhQuerySales.updatePrintPage(this.printer._id, value);
  }
  printTemp() {
    let invoice = new VhSaleVoucherInvoices(this.tempBillSalesVoucher, this.bill_details_SalesVoucher, this.vhQuerySales);
    this.vhComponent.showModal(this.renderPrintSalesPage(this.item._id), { printer: this.item, invoice: invoice }, false, false, `modal-print-${this.item['_id']}`)
  }
  
  private renderPrintSalesPage(type: 'print_size_a4' | 'print_size_a5' | 'print_size_k80_4c' | 'print_size_k80_3c' | 'print_size_k57_3c') {
    switch (type) {
      case 'print_size_k57_3c': return SalesVoucherK57Col3Component
      case 'print_size_k80_3c': return SalesVoucherK80Col3Component
      case 'print_size_k80_4c': return SalesVoucherK80Col4Component
      case 'print_size_a5': return SalesVoucherA5Component
      case 'print_size_a4': return SalesVoucherA4Component
      default: return SalesVoucherA4Component
    }
  }

